.editor-container {
  min-height: 100%;
}

.editor-scroll {
  z-index: 1;
}

.editor {
  font-size: inherit;
  font-weight: inherit;
  color: inherit;
  background-color: inherit;
}

.editor-resize {
  padding: 1px 0;
  cursor: ns-resize;
}
.editor-resize hr {
  width: 25px;
  margin: 1px auto 0;
}

.editor-source {
  resize: none;
}

.editor-img-highlight {
  background-color: var(--ui-border-color-translucent);
  border: var(--ui-border-width) dashed var(--ui-body-color);
}

.editor-img-cursor {
  width: 2rem;
  height: 2rem;
}

.editor-img-size {
  color: var(--ui-body-bg);
  background-color: var(--ui-tertiary-color);
}

.editor-img-resize {
  width: 5px;
  height: 5px;
  cursor: nwse-resize;
  background-color: var(--ui-body-color);
}

.editor-drop-target {
  z-index: 1;
  min-height: 100%;
  background-color: var(--ui-body-bg);
}

.editor-popover {
  max-width: initial;
}

.editor-link-url {
  max-width: 200px;
  vertical-align: middle;
}

.editor-color {
  height: 20px;
}

.editor-table-link {
  margin-right: 1px;
  margin-bottom: 1px;
  opacity: 0.5;
}

.btn-editor {
  --ui-btn-bg: transparent;
  --ui-btn-border-color: transparent;
  --ui-btn-color: var(--ui-body-color);
  --ui-btn-hover-bg: var(--ui-tertiary-bg);
  --ui-btn-hover-border-color: transparent;
  --ui-btn-active-bg: rgba(var(--ui-body-color-rgb), 0.2);
  --ui-btn-focus-shadow-rgb: var(--ui-tertiary-color-rgb);
}

.btn-outline-editor {
  --ui-btn-bg: transparent;
  --ui-btn-border-color: var(--ui-body-color);
  --ui-btn-color: var(--ui-body-color);
  --ui-btn-hover-bg: rgba(var(--ui-body-color-rgb), 0.2);
  --ui-btn-hover-border-color: var(--ui-body-color);
  --ui-btn-active-bg: var(--ui-body-color);
  --ui-btn-active-color: var(--ui-body-bg);
  --ui-btn-active-border-color: var(--ui-body-color);
  --ui-btn-focus-shadow-rgb: var(--ui-body-color-rgb);
}

